:root {
    --bg: #ffffff;
    --text: #2d3748;
    --secondary: #718096;
    --accent: #475569;
    --accent-light: #64748b;
    --code-bg: #f7fafc;
    --border: #e2e8f0;
    --gradient: linear-gradient(135deg, #475569 0%, #64748b 100%);
}

[data-theme="dark"] {
    --bg: #0f172a;
    --text: #f1f5f9;
    --secondary: #94a3b8;
    --accent: #64748b;
    --accent-light: #94a3b8;
    --code-bg: #1e293b;
    --border: #334155;
    --gradient: linear-gradient(135deg, #64748b 0%, #94a3b8 100%);
}

.title {
    font-family: 'Roboto Slab', serif;
}
body {
    font-family: 'Roboto Slab', serif;
    font-size: 1.2rem;
    line-height: 1.5;
    color: var(--text);
    background: var(--bg);
    max-width: 800px;
    margin: 0 auto;
    padding: 2rem;
    transition: all 0.3s ease;
}

h1 {
    font-size: 2.5rem;
    line-height: 1.2;
    margin: 2rem 0;
}

h2 {
    font-size: 1.8rem;
    line-height: 1.3;
    margin: 3rem 0 1.5rem;
}

h3 {
    font-size: 1.4rem;
    line-height: 1.4;
    margin: 2rem 0 1rem;
}

.post-meta {
    font-size: 1rem;
}

code {
    font-size: 0.95rem;
}

.theme-toggle {
    font-size: 0.95rem;
}

nav h1 {
    font-size: 1.8rem;
    margin: 0;
}

.nav-links {
    font-size: 1rem;
}

article img {
    margin: 3rem 0;
    border-radius: 8px;
    border: 1px solid var(--border);
    transition: filter 0.3s ease;
}

article img:hover {
    filter: brightness(1.05);
}

article p {
    margin: 1.7rem 0;
}

article pre {
    border-radius: 0;
    background: var(--code-bg);
    border-left: 4px solid var(--accent);
    overflow-x: auto;
}

@media (max-width: 768px) {
    article pre {
        margin: 2rem 0;
        border-radius: 0;
        border-left: none;
        border-top: 4px solid var(--accent);
    }
}

blockquote {
    margin: 2rem 0;
    padding: 1.5rem 2rem;
    border-left: 4px solid var(--accent);
    background: var(--code-bg);
    border-radius: 0;
}

article h2::before {
    content: "";
    display: block;
    width: 2rem;
    height: 2px;
    background: var(--accent);
    margin-bottom: 1rem;
}

ul, ol {
    padding-left: 1.5rem;
    margin: 1.7rem 0;
}

li {
    margin: 0.8rem 0;
}

::selection {
    background: var(--accent-light);
    color: white;
}

h1, h2, h3 {
    font-family: 'Plus Jakarta Sans', sans-serif;
    color: var(--accent);
}

.theme-toggle {
    position: fixed;
    top: 1rem;
    right: 1rem;
    padding: 0.5rem 1rem;
    background: var(--gradient);
    color: white;
    border: none;
    border-radius: 99px;
    cursor: pointer;
    font-family: 'Space Grotesk', sans-serif;
    font-size: 0.9rem;
    transition: transform 0.2s ease;
}

.theme-toggle:hover {
    transform: scale(1.05);
}

article {
    margin-bottom: 4rem;
}

pre {
    background: var(--code-bg);
    border-radius: 4px;
    overflow-x: auto;
}

code {
    font-family: 'Inconsolata', monospace;
    font-size: 0.9em;
}

img {
    max-width: 100%;
    height: auto;
    border-radius: 4px;
    margin: 2rem 0;
}

ul, ol {
    padding-left: 1.5rem;
}

li {
    margin-bottom: 0.5rem;
}

nav {
    margin-bottom: 3rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid var(--border);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-links {
    display: flex;
    gap: 2rem;
}

.nav-links a {
    color: var(--text);
    text-decoration: none;
    font-family: 'Plus Jakarta Sans', sans-serif;
}

.nav-links a:hover,
.nav-links a.active {
    color: var(--accent);
}

blockquote {
    border-left: 4px solid var(--accent);
    margin-left: 0;
    padding-left: 1rem;
    color: var(--secondary);
}

.post-list {
    list-style: none;
    padding: 0;
}

.post-item {
    margin-bottom: 2rem;
    padding: 1.5rem;
    border-radius: 8px;
    transition: all 0.2s ease;
    border: 1px solid var(--border);
}

.post-item:hover {
    transform: translateY(-2px);
    border-color: var(--accent);
}

.post-title {
    font-family: 'Plus Jakarta Sans', sans-serif;
    font-size: 1.5rem;
    margin: 0 0 0.5rem 0;
}

.post-title a {
    color: var(--accent);
    text-decoration: none;
    background: linear-gradient(to right, var(--accent), var(--accent));
    background-size: 0 2px;
    background-position: 0 100%;
    background-repeat: no-repeat;
    transition: background-size 0.3s ease;
}

.post-title a:hover {
    background-size: 100% 2px;
}

.post-meta {
    color: var(--secondary);
    font-size: 0.9rem;
    margin-bottom: 1rem;
}

.post-excerpt {
    margin: 0;
}

.left {
    float: left;

    margin: 0 30px 30px 0 ;
}

.meeting-grid{
display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 20px;
  padding: 20px;

}
.meeting-card{
  text-decoration: none;
  text-align: center;
  border: 1px solid var(--text);
  border-radius: 8px;
  padding: 20px;
}

.meeting-card > p {
    margin: 10px;
}

.meeting-grid h2 {
  margin: 10px;
  text-decoration: none;
  min-height: 70px
}

span {
  text-decoration: none;
  color: var(--text);
}

.duration {
  text-decoration: none;
  color: var(--text);
}

svg {
  stroke: var(--text);
  fill: var(--bg);
}

.icon {
  width: 50px;
  height: 50px;
}

